@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{font-family:'Open Sans Condensed', sans-serif}*{box-sizing:border-box}.container{max-width:1140px;width:100%;margin:0 auto;padding:0 15px}.container-fluid{width:100%;padding:0 15px}.row{margin:0 -15px}.character-body{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto auto auto auto;grid-gap:30px 30px;font-size:14px}.character-body-col{width:100%}.character-body-col-group{display:flex;width:100%;align-items:flex-start}.character-body-stats-col{margin-left:15px;width:100%}.form-input{display:block;border:none;width:100%;background-color:transparent}.form-input-label{display:block;text-transform:uppercase}.form-input-underline-label{margin-right:5px;display:inline-block}.form-input-underline{border:none;border-bottom:1px solid #bdbdbd;background-color:transparent;text-align:center}.form-row{display:flex}.check-group{width:100%;padding-top:5px}.check-input{display:flex;position:relative;padding-left:5px;margin-bottom:5px;cursor:pointer;font-size:14px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.check-input__checkbox-icon{display:inline-block;border:1px solid #505050;border-radius:50%;width:10px;height:10px}.check-input__checkbox-icon::after{content:'';display:none;width:10px;height:10px;display:block;background-color:#505050}.check-input__input-wrap{width:25px;margin:0 5px}.check-input__input-wrap::after{content:'';display:block;width:25px;height:1px;background-color:black}.check-input__input{display:block;width:100%;border:none;text-align:center}.check-input__input:focus{outline:none}.check-group__text{display:block;text-align:center;font-weight:bold;line-height:1;margin:10px 0 10px 0}body{background-color:#f7f7f7}body *{color:#505050;box-sizing:border-box}.centered{display:block;text-align:center}.flex{display:flex}.header{margin-bottom:30px}

.character-header{display:flex;width:100%;margin-bottom:30px}.character-header__name{width:40%;min-height:170px;background-image:url(/dnd-character/static/media/headerChar.55288956.svg);background-repeat:no-repeat;background-size:contain;padding:100px 40px 0}.character-header__name label{display:block;margin-top:50px;text-transform:uppercase}.character-header__info{width:60%;min-height:190px;background-image:url(/dnd-character/static/media/headerInfo.c92812d6.svg);background-repeat:no-repeat;background-size:contain;background-position:100% 90%;-webkit-transform:translateX(-4px);transform:translateX(-4px);padding:60px 40px 20px 40px}.character-header__info-row:first-of-type{margin-bottom:15px}

.character-stats{max-width:125px;width:100%;background-color:#efefef;padding:25px 15px}.character-stats__item{background-image:url(/dnd-character/static/media/stat.26ddf4a1.svg);background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;width:100%;min-height:110px;text-align:center;padding:8px 0 6px 0;margin-bottom:15px}.character-stats__item-label{font-weight:bold}.character-stats__item-input-ability-score{display:block;max-width:48px;width:100%;margin:auto auto 2px;border:none;background-color:transparent;text-align:center;padding:8px 0 0;border-radius:50%;font-size:16px}.character-stats__item-input-ability-score:focus{outline:none}.character-stats__item-input-ability-modifier{width:100%;padding:9px 0;font-size:20px;max-width:60px;margin:0 auto;border:none;background:transparent;text-align:center;font-weight:bold}

.character-inspiration{width:100%;display:flex;min-height:40px;margin-bottom:15px}.character-inspiration-btn{position:relative;display:flex;height:100%}.character-inspiration-btn svg{align-self:center}.character-inspiration-btn:hover{cursor:pointer}.character-inspiration__input{display:block;position:absolute;top:50%;left:50%;width:100%;height:100%;background:transparent;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border:none;text-align:center;font-size:20px}.character-inspiration__body{display:flex;align-self:center;width:100%;min-height:40px;background-image:url(/dnd-character/static/media/inspiration.018f3db6.svg);background-repeat:no-repeat;background-size:contain;padding:0 15px 0 10px;text-align:center}.character-inspiration__body span{align-self:center;display:block;width:100%}

.character-proficiency{width:100%;display:flex;margin-bottom:10px}.character-proficiency__head{background-repeat:no-repeat;background-size:cover}.character-proficiency-btn{position:relative;display:flex;height:100%}.character-proficiency-btn:hover{cursor:pointer}.character-proficiency__input{display:block;position:absolute;top:50%;left:50%;width:100%;height:100%;background:transparent;-webkit-transform:translate(-53%, -50%);transform:translate(-53%, -50%);border:none;text-align:center;font-size:20px}.character-proficiency__body{display:flex;align-self:center;width:100%;min-height:43px;padding:0 15px 0 10px;background-image:url(/dnd-character/static/media/proficiencyBonusBody.d0abc420.svg);background-repeat:no-repeat;background-size:contain;text-align:center;-webkit-transform:translate(-8px, -1px);transform:translate(-8px, -1px)}.character-proficiency__body span{align-self:center;display:block;width:100%}

.character-saving-throws{background-image:url(/dnd-character/static/media/savingThrows.e5436cda.svg);background-repeat:no-repeat;background-size:contain;min-height:190px;padding:15px 5px}

.character-skill-scores{background-image:url(/dnd-character/static/media/skillScores.50e7bb51.svg);background-repeat:no-repeat;background-size:contain;min-height:490px;padding:15px 5px}

.character-passive-wisdom{display:flex;align-items:flex-start;width:100%}.character-passive-wisdom__head-btn{position:relative;display:block}.character-passive-wisdom__input{display:block;position:absolute;top:50%;left:50%;width:100%;height:100%;background:transparent;-webkit-transform:translate(-45%, -50%);transform:translate(-45%, -50%);border:none;text-align:center;font-size:20px}.character-passive-wisdom__body{width:100%;min-height:45px;padding:13px 0;align-self:center;background-image:url(/dnd-character/static/media/passiveWisdomBody.dead8d7e.svg);background-repeat:no-repeat;background-size:contain;-webkit-transform:translateX(-3px);transform:translateX(-3px)}.character-passive-wisdom__body span{display:block;text-align:center}

.character-proficiency-other{min-height:280px;background-image:url(/dnd-character/static/media/proficiencyOther.e0b33c7c.svg);background-repeat:no-repeat;background-size:contain;padding:10px 15px}.character-proficiency-other__textarea{display:block;width:100%;height:100%;resize:none;background-color:transparent;border:none}.character-proficiency-other__text{display:block;text-transform:uppercase;text-align:center}

.character-combat-stats{padding:10px 10px 0;background-color:#efefef}.character-combat-stats-row{display:flex;margin-bottom:5px}.character-combat-stats__initiative,.character-combat-stats__speed,.character-combat-stats__armor{display:flex;flex-direction:column;min-height:100px;width:100px;background-image:url(/dnd-character/static/media/box.6f40b892.svg);background-repeat:no-repeat;background-size:contain;margin-right:20px}.character-combat-stats__initiative:last-of-type,.character-combat-stats__speed:last-of-type,.character-combat-stats__armor:last-of-type{margin-right:0}.character-combat-stats__armor{min-height:103px;width:90px;background-image:url(/dnd-character/static/media/armor.42ffbf72.svg)}.character-combat-stats__input{display:block;width:100%;max-width:60px;margin:auto auto 0;padding:5px 0 0;text-align:center;border:none;font-size:20px;background-color:transparent}.character-combat-stats__text{display:block;text-align:center;text-transform:uppercase;font-size:12px;margin-bottom:20px}.character-combat-stats__hitpoints .character-combat-stats__text{margin-bottom:0}.character-combat-stats__hitpoints-current{min-height:125px;padding:12px;background-image:url(/dnd-character/static/media/hitPointsCurrent.8f2b811e.svg);background-repeat:no-repeat;background-size:contain}.character-combat-stats__hitpoints-current-text{margin-right:5px;display:inline-block}.character-combat-stats__hitpoints-current-input{border:none;border-bottom:1px solid #bdbdbd;background-color:transparent;text-align:center}.character-combat-stats__hitpoints-current-wrap{display:flex;margin-bottom:10px}.character-combat-stats__hitpoints-current-textarea{display:block;width:100%;background:transparent;resize:none;border:none;margin-bottom:10px}.character-combat-stats__hitpoints-temp{display:flex;flex-direction:column;padding:10px;background-image:url(/dnd-character/static/media/hitPointsTemp.e0566c0e.svg);background-repeat:no-repeat;background-size:contain;min-height:105px}.character-combat-stats__hitpoints-temp-textarea{border:none;resize:none;background:transparent;margin-bottom:5px}.character-combat-stats__hit,.character-combat-stats__death-saves{display:flex;flex-direction:column;min-height:105px;width:50%;margin-right:7px;padding:14px;background-image:url(/dnd-character/static/media/boxMd.045d508a.svg);background-repeat:no-repeat;background-size:contain}.character-combat-stats__hit:last-of-type,.character-combat-stats__death-saves:last-of-type{margin-right:0;margin-left:7px}.character-combat-stats__hit .character-combat-stats__text,.character-combat-stats__death-saves .character-combat-stats__text{margin-top:auto;margin-bottom:0}.character-combat-stats__hit input{display:block;width:100%}.character-combat-stats__death-saves-textarea{display:block;width:100%;resize:none;border:none;background:transparent}

.character-attack-spellcasting{display:block;width:100%;min-height:380px;margin-bottom:15px;padding:10px 15px;background-image:url(/dnd-character/static/media/attacksSpellcasting.283f3703.svg);background-repeat:no-repeat;background-size:contain}.character-attack-spellcasting__table input{display:block;width:100%;background:#e2e2e2;padding:5px}.character-attack-spellcasting__table th:first-of-type,.character-attack-spellcasting__table td{width:30px}.character-attack-spellcasting__table-head{display:flex}.character-attack-spellcasting__table-head-text{text-transform:uppercase;font-size:12px;margin-bottom:5px}.character-attack-spellcasting__table-body-row{display:flex;margin-bottom:5px}.character-attack-spellcasting__table-item{margin-bottom:5px;border:none}.character-attack-spellcasting__table-item:first-of-type,.character-attack-spellcasting__table-item:last-of-type{width:110px}.character-attack-spellcasting__table-item:nth-child(2){width:70px;margin:0 15px 5px}.character-attack-spellcasting__textarea{display:block;width:100%;resize:none;border:none;margin-bottom:15px}.character-attack-spellcasting__text{display:block;text-align:center;text-transform:uppercase}

.character-equipment{width:100%;min-height:340px;padding:10px 15px 10px 22px;background-image:url(/dnd-character/static/media/equipment.466d2713.svg);background-repeat:no-repeat;background-size:contain;-webkit-transform:translateX(-5px);transform:translateX(-5px)}.character-equipment-wrap{display:flex;margin-bottom:6px}.character-equipment__stats{width:87px;padding:9px 0}.character-equipment__stats-item{display:flex;-webkit-transform:translateX(-16px);transform:translateX(-16px);margin-bottom:14px;align-items:center}.character-equipment__stats-item-text{display:flex;width:20px;margin-right:10px;font-size:12px}.character-equipment__stats-item-input{display:block;width:100%;padding:10px 0;border:none;background:transparent;text-align:center}.character-equipment__textarea{margin-left:10px}.character-equipment__textarea textarea{width:100%;border:none;background:transparent;resize:none}

